import React from 'react';
import { Button, TextField } from '@mui/material';
import '../css/Login.scss';

const Login = () => {
  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Login form submitted');
  };

  return (
    <div className='outerContainer'>
      <div className="login container">
        <div className='header'>
          <h1>Co-Sync</h1>
          <h2>Login</h2>
        </div>
        <div className='login'>
          <form className='formContainer'>
            <TextField sx={{input: {color: 'red', background: 'transparent'}}} className='formInput' label="Username" variant="outlined" />
            <TextField className='formInput' label="Password" variant="outlined" type='password' />
            <Button variant="contained" className='formButton' type='submit' onClick={ handleSubmit }>Login</Button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Login;
